* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  background-color: #d2dbdd;
}

.heading_style {
  padding: 30px 0;
  text-align: center;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: 500;
  color: #868686;
  background-color: #fff;
  margin-bottom: 30px;
  font-size: 1.5rem;
}

button {
  padding: 5px 8px;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 500;
  color: #868686;
  background-color: #fff;
  outline: none;
  cursor: pointer;
  border: 1px solid palegreen;
}
.cards {
  width: 100%;

  height: auto;
}

.card {
  margin: 6%;

  transition: all 0.4s cubic-bezier(0.175, 0.885, 0, 1);

  background-color: cornsilk;

  width: 21.25%;

  border-radius: 12px;

  box-shadow: 0px 13px -7px rgba(0, 0, 0, 0.1);

  float: left;
}

.card:hover {
  box-shadow: 0px 30px 18px -8px rgba(0, 0, 0, 0.1);
  transform: scale(1.05, 1.05);
}

.card_img {
  width: 90%;
  height: 300px;
}
